<template>
  <section class="center-add bg-white border border-1 border-slate-300 rounded-sm mx-1" :style="{height:heightBox+'px'}">
    <div class="container mx-auto p-1">
      <div class="w-full" style="height: 300px">
        <div class="flex flex-row justify-between">
          <div ><font-awesome-icon  @click="plusCount" :icon="['fas','fa-angle-double-right']" class="text-sm text-slate-500 text-green-500"></font-awesome-icon></div>
          <div ><font-awesome-icon @click="minesCount" :icon="['fas','fa-angle-double-left']" class="text-sm text-slate-500 text-red-500"></font-awesome-icon></div>
        </div>
        <div class="flex flex-col space-y-3">
          <div class="h-20">
            <div style="height: 200px">
              <img :src="meImage" alt="" class="myImage w-full h-full">
            </div>
            <div class="mt-8">
              <p class="textDone">{{text1}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="flex flex-col space-y-3">
        <p class="text-sm text-right font-semibold">
          مطالب پیشنهادی
        </p>
        <div class="bg-slate-800 h-0.5 my-2"></div>
      </div>

      <div class="flex flex-col space-y-2 w-full">
        <div v-for="value in list" :key="value" class="flex flex-row justify-between my-3 w-full">
          <div class="w-6/12 h-20">
            <img :src="`${value.img}`" alt="" class="w-full h-full">
          </div>
          <div class="mr-2 w-6/12 h-20">
            <p class="text-right font-semibold text-sm text-slate-700">{{value.title}}</p>
          </div>
        </div>
      </div>
      <div class="add" >
        <img :src="require('/src/assets/image/add.jpg')" alt="" class="m-auto" style="height: 480px">
      </div>
    </div>
  </section>
</template>

<script>
import {reactive, ref} from "vue";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
export default {
  name: "ExampleAdd",
  components:{FontAwesomeIcon},
  setup(){
    const heightBox=ref(1200)
    const text1=ref('')
    const count=ref(0)
    const list=reactive([
      {img:[require('/src/assets/image/dollar-1.jpg')],title:'با این زانوبند دیگه برای هیمشه از درد زانو راحت میشی'},
      {img:[require('/src/assets/image/dollar-2.jpg')],title:' دلار در روز با گوشی'},
      {img:[require('/src/assets/image/dollar-3.png')],title:' دریافت قیمت به روز و آنلاین ورق، با مناسب‌ترین قیمت خرید کن!!'},
      {img:[require('/src/assets/image/dollar-4.jpg')],title:'۲۰۰۰ دلار در ماه با انجام کارهای ساده'},
    ])
    const meImage=ref(list[0])
    function plusCount(){
      count.value++;
      if(count.value > list.length-1){
        count.value=0
      }
      meImage.value=list[count.value].img
      text1.value=list[count.value].title

    }

    function minesCount() {
      count.value--;
      if (count.value < 0) {
        count.value = list.length - 1
      }
      meImage.value = list[count.value].img
      text1.value= list[count.value].title
    }

    setInterval(()=>{
      plusCount()
    },3000)
    return{
      meImage,
      heightBox,
      text1,
      list,
      count,
      minesCount,
      plusCount
    }
  }
}
</script>

