<template>
  <section id="scroll"  class=" scroll center-best-wordlcup my-1 border border-1 border-slate-300 rounded-sm p-1" :style="{height:heightBox+'px'}">
    <div class="container mx-auto p-1">
      <p class="text-lg font-thin text-slate-900 text-start text-right">برترین های فصل 2022/2023</p>
      <div  class="w-full h-auto">
        <div  class="flex flex-row align-center justify-between p-1 bg-gray-300 w-full ">
          <div  @click="bestGoals(1)"><button @click="tabs='AllGoals'" type="button" class="px-3 py-1 outline-none btn  text-white rounded-sm text-sm" :class="{black : count===1 }">گل</button></div>
          <div  @click="bestGoals(2)"><button @click="tabs='AllAssits'" type="button" class="px-3 py-1 outline-none btn  text-white  rounded-sm text-sm"  :class="{black: count===2  }">پاس گل</button></div>
          <div  @click="bestGoals(3)"><button @click="tabs='AllAssitsGoal'" type="button" class="px-3 py-1 outline-none btn  text-white  rounded-sm text-sm"  :class="{black: count===3 }">مجموع</button></div>
        </div>
      </div>
      <Transition :duration="500" name="fade" mode="out-in">
      <keep-alive>
        <component :is="tabs"></component>
      </keep-alive>
      </Transition>
    </div>
  </section>
</template>

<script>
import{defineAsyncComponent} from "vue";
const AllGoals=defineAsyncComponent(()=>import('@/components/main/EnglandLeage/AllGoals'))
const AllAssits=defineAsyncComponent(()=>import('@/components/main/EnglandLeage/AllAssits'))
const AllAssitsGoal=defineAsyncComponent(()=>import('@/components/main/EnglandLeage/AllAssitsGoal'))

import{ref} from 'vue'
export default {
  name: "BestSeason",
  components: {AllAssitsGoal, AllAssits, AllGoals},
  setup(){
    const tabs=ref('AllGoals')
    const count=ref(1)
    const heightBox=ref(600)
    function bestGoals(data){
      count.value=data
    }
    return{
      tabs,count,heightBox,bestGoals,
    }
  }
}
</script>

<style scoped>
.black{
  background-color: #373738;
}
.scroll{
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color: grey;
}
#scroll::-webkit-scrollbar-track{
  background-color: #fff;
  -webkit-box-shadow: inset 0 0 0 6px rgba(0,0,0,0.3);
}
.fade-enter-active,
.fade-leave-active{
  transition: all ease-in-out 0.5s;
}
.fade-enter-from,
.fade-leave-to{
  transition: inherit;
  opacity: 0;
}
</style>