<template>
<example-get-news :my-list="list">
<template #teamName>
  <p class="text-sm text-right font-semibold my-1 text-slate-900 my-2">آخرین اخبار لیگ انگلیس</p>
</template>
</example-get-news>
</template>

<script>
import{reactive,onMounted} from 'vue';
import{defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
const ExampleGetNews=defineAsyncComponent(()=>import('@/components/main/ExampleGetNews'))
import {handleError} from "@/components/composable/handleError";
export default {
  name: "NewsLeage",
  components: {ExampleGetNews},
  setup(){
    const list=reactive([])
    const result=onMounted(()=>{
      EndPoints.getGoalsEngland()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data.news)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      list,result,
    }
  }
}
</script>

